body,
button,
input,
select,
optgroup,
textarea {
	color: $color__text-main;
	font-family: $font__opensans;
	@include font-size(.9);
	line-height: $font__line-height-body;

	@media screen and (min-width: 700px) {
		@include font-size(1);
	}
}

p {
	@include font-size(.8);

	@media screen and (min-width: 700px) {
		@include font-size(.9);
	}
}

@import "headings";

@import "copy";
